import React from 'react'
import { Link } from 'react-router-dom'

const Home = () => {
    return (
        <div className="container">
            <div class="jumbotron mt-4">
                <h1 class="display-4 text-capitalize">Welcome to tech blog</h1>
                <p class="lead"> We make all kinds of awesome blog about various technology topics.</p>
                <hr class="my-4" />
                <p>Click the button below to check our awesome blog.</p>
                <Link class="btn btn-primary btn-lg" to='/blog' role="button">Check Out Our Blogs</Link>
            </div>
        </div>
    )
}

export default Home
