import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom'

const Category = (props) => {
    const [blogs, setBlogs] = useState([]);
    const [currentCategory, setCurrentCategory] = useState([]);

    useEffect(() => {
        const category = props.match.params.id;
        setCurrentCategory(capitalizeFirstLetter(category));
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        };
        const fetchData = async () => {
            try {
                const res = await axios.post(`${process.env.REACT_APP_API_URL}/api/blog/category`, { category }, config);
                setBlogs(res.data);
            }
            catch (err) {

            }
        };

        fetchData();
    }, [props.match.params.id]);

    const capitalizeFirstLetter = (word) => {
        if (word)
            return word.charAt(0).toUpperCase() + word.slice(1)
        return '';
    };

    const getCategoryBlogs = () => {
        let list = [];
        let result = [];

        blogs.map(blogPost => {
            return list.push(
                <div className="card flex-md-row mb-4 box-shadow h-md-250">
                    <div className="card-body d-flex flex-column align-items-start">
                        <strong className="d-inline-block mb-2 text-success">{capitalizeFirstLetter(blogPost.category)}</strong>
                        <h3 className="mb-0">
                            <a className="text-dark" href="#">{blogPost.title}</a>
                        </h3>
                        <div className="mb-1 text-muted">{blogPost.month}</div>
                        <p className="card-text mb-auto">{blogPost.excerpt}</p>
                        <Link to={`/blog/${blogPost.slug}`} href="#">Continue reading</Link>
                    </div>
                    <img height="250" width="200" className="card-img-right flex-auto d-none d-md-block" src={blogPost.thumbnail} alt="Thumbnail Picture Here" />
                </div>
            );
        });
        for (let i = 0; i < list.length; i += 2) {
            result.push(
                <div key={i} className='row mb-2'>
                    <div className='col-md-6'>
                        {list[i]}
                    </div>
                    <div className='col-md-6'>
                        {list[i + 1] ? list[i + 1] : null}
                    </div>
                </div>
            )
        }
        return result;
    };
    return (
        <div className="container mt-3">
            <h5 className="display-6">/category/ {currentCategory}</h5>
            <div className="nav-scroller py-1 mb-2">
                <nav className="nav d-flex justify-content-between">
                    <Link className="p-2 text-muted font-weight-bold" to='/category/world'>World</Link>
                    <Link className="p-2 text-muted font-weight-bold" to='/category/environment'>Environment</Link>
                    <Link className="p-2 text-muted font-weight-bold" to='/category/technology'>Technology</Link>
                    <Link className="p-2 text-muted font-weight-bold" to='/category/education'>Education</Link>
                    <Link className="p-2 text-muted font-weight-bold" to='/category/science'>Science</Link>
                    <Link className="p-2 text-muted font-weight-bold" to='/category/health'>Health</Link>
                    <Link className="p-2 text-muted font-weight-bold" to='/category/travel'>Travel</Link>
                </nav>
            </div>
            {getCategoryBlogs()}
        </div>
    )
}

export default Category
