import React from 'react'
import { NavLink } from 'react-router-dom'
import './css/Custom.css';

const NavBar = () => {
    return (
        <div className="container">
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark text-white">
                <NavLink className="navbar-brand text-uppercase" exact to='/'>Tech Blog</NavLink>
                <button
                    className="navbar-toggler"
                    type="button"
                    data-toggle="collapse"
                    data-target="#navbarNavAltMarkup"
                    aria-controls="navbarNavAltMarkup"
                    aria-expanded="false"
                    aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                    <div className="navbar-nav">
                        <NavLink className="nav-link text-uppercase" exact to='/'>Home <span className="sr-only">(current)</span></NavLink>
                        <NavLink className="nav-link text-uppercase" exact to='/blog'>Blog</NavLink>
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default NavBar
